<template>
  <div>
    <div class="mask" v-if="isloading">L O A D I N G ...</div>
    <div id="controls" class="controls">
      <div
        class="point"
        :style="{bottom: pointBottom + 'px', left: pointLeft + 'px'}"
        @touchstart.prevent="handleTouchStart"
        @touchmove="handleTouchMove"
        @touchend="handleTouchEnd"
      ></div>
    </div>
    <div class="attack" @click="attack"></div>
  </div>
</template>
<script>
import * as THREE from "../../static/three.js/build/three.module.js";
import { OrbitControls } from "../../static/three.js/examples/jsm/controls/OrbitControls.js";
import { MD2CharacterComplex } from '../../static/three.js/examples/jsm/misc/MD2CharacterComplex.js';
import { Gyroscope } from '../../static/three.js/examples/jsm/misc/Gyroscope.js';

export default {
  name: "Main",
  components: {},
  data() {
    return {
      SCREEN_WIDTH: window.innerWidth,
      SCREEN_HEIGHT: window.innerHeight,

      container: null,
      camera: null,
      scene: null,
      light: null,
      renderer: null,
      characters: [],
      nCharacters: 0,
      cameraControls: null,
      controls: {
        moveForward: false,
        moveBackward: false,
        moveLeft: false,
        moveRight: false
      },
      clock: new THREE.Clock(),
      isloading: true,

      mainCharacter: null,
      posX: -75,
      posZ: 0,

      pointBottom: 90,
      pointLeft: 90,
    };
  },
  computed: {},
  mounted() {
    this.init();
    this.animate();
  },
  watch: {
    pointBottom(val, old) {
      if (val > 90) {
        this.controls.moveForward = true;
      }
    },
    pointLeft(val, old) {
      if (val > 90) {
        if (this.controls.moveRight) return;
        this.controls.moveRight = true;
        setTimeout(_ => this.controls.moveRight = false, 500);
      }
      if (val < 90) {
        if (this.controls.moveLeft) return;
        this.controls.moveLeft = true;
        setTimeout(_ => this.controls.moveLeft = false, 500);
      }
    }
  },
  methods: {
    init() {
      this.container = document.createElement('div');
      document.body.appendChild(this.container);
      // CAMERA
      this.camera = new THREE.PerspectiveCamera(45, window.innerWidth / window.innerHeight, 1, 4000);
      this.camera.position.set(0, 150, 1300);
      // SCENE
      this.scene = new THREE.Scene();
      this.scene.background = new THREE.Color(0xffffff);
      this.scene.fog = new THREE.Fog(0xffffff, 1000, 4000);
      this.scene.add(this.camera);
      // LIGHTS
      this.scene.add(new THREE.AmbientLight(0x222222));
      var light = this.light = new THREE.DirectionalLight(0xffffff, 2.25);
      light.position.set(200, 450, 500);
      light.castShadow = true;
      light.shadow.mapSize.width = 1024;
      light.shadow.mapSize.height = 512;
      light.shadow.camera.near = 100;
      light.shadow.camera.far = 1200;
      light.shadow.camera.left = - 1000;
      light.shadow.camera.right = 1000;
      light.shadow.camera.top = 350;
      light.shadow.camera.bottom = - 350;
      this.scene.add(light);
      // scene.add( new CameraHelper( light.shadow.camera ) );

      // SKYBOX
      this.scene.background = new THREE.CubeTextureLoader()
        .setPath("./static/skybox/night/")
        .load([
          "posx.jpg",
          "negx.jpg",
          "posy.jpg",
          "negy.jpg",
          "posz.jpg",
          "negz.jpg"
        ]);

      // RENDERER
      this.renderer = new THREE.WebGLRenderer({ antialias: true });
      this.renderer.setPixelRatio(window.devicePixelRatio);
      this.renderer.setSize(this.SCREEN_WIDTH, this.SCREEN_HEIGHT);
      this.container.appendChild(this.renderer.domElement);

      this.renderer.gammaInput = true;
      this.renderer.gammaOutput = true;

      this.renderer.shadowMap.enabled = true;
      this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;

      //  GROUND
      let helper = new THREE.GridHelper(1200, 60, 0x000000, 0x101010);
      this.scene.add(helper);

      // EVENTS
      window.addEventListener('resize', this.onWindowResize, false);
      document.addEventListener('keydown', this.onKeyDown, false);
      document.addEventListener('keyup', this.onKeyUp, false);

      // CONTROLS
      this.cameraControls = new OrbitControls(this.camera, this.renderer.domElement);
      this.cameraControls.target.set(0, 50, 0);
      this.cameraControls.maxPolarAngle = Math.PI * 0.45;
      this.cameraControls.minPolarAngle = Math.PI * 0.3;
      this.cameraControls.minDistance = 800;
      this.cameraControls.maxDistance = 1000;
      this.cameraControls.enablePan = false;
      this.cameraControls.update();

      // CHARACTER
      var configOgro = {
        baseUrl: "./static/resource/models/md2/ratamahatta/",
        body: "ratamahatta.md2",
        skins: ["ctf_r.png"],
        weapons: [
          ["weapon.md2", "weapon.png"],
          ["w_bfg.md2", "w_bfg.png"],
          ["w_blaster.md2", "w_blaster.png"],
          ["w_chaingun.md2", "w_chaingun.png"],
          ["w_glauncher.md2", "w_glauncher.png"],
          ["w_hyperblaster.md2", "w_hyperblaster.png"],
          ["w_machinegun.md2", "w_machinegun.png"],
          ["w_railgun.md2", "w_railgun.png"],
          ["w_rlauncher.md2", "w_rlauncher.png"],
          ["w_shotgun.md2", "w_shotgun.png"],
          ["w_sshotgun.md2", "w_sshotgun.png"]
        ],
        animations: {
          move: "run",
          idle: "stand",
          jump: "jump",
          salute: "salute",
          attack: "attack",
          crouchMove: "cwalk",
          crouchIdle: "cstand",
          crouchAttach: "crattack"
        },
        walkSpeed: 200,
        crouchSpeed: 50
      }
      var configOgro2 = {
        baseUrl: "./static/resource/models/md2/ogro/",
        body: "ogro.md2",
        skins: ["grok.jpg"],
        weapons: [["weapon.md2", "weapon.jpg"]],
        animations: {
          move: "run",
          idle: "stand",
          jump: "jump",
          salute: "salute",
          attack: "attack",
          crouchMove: "cwalk",
          crouchIdle: "cstand",
          crouchAttach: "crattack"
        },
        walkSpeed: 200,
        crouchSpeed: 5
      };

      var nRows = 1;
      var nSkins = configOgro.skins.length;

      this.nCharacters = nSkins * nRows;
      for (var i = 0; i < this.nCharacters; i++) {
        var character = new MD2CharacterComplex();
        character.scale = 3;
        character.controls = this.controls;
        this.characters.push(character);
      }
      var baseCharacter = new MD2CharacterComplex();
      baseCharacter.scale = 3;
      const that = this;
      baseCharacter.onLoadComplete = function () {
        that.isloading = false;
        var k = 0;
        for (var j = 0; j < nRows; j++) {
          for (var i = 0; i < nSkins; i++) {
            var cloneCharacter = that.characters[k];
            cloneCharacter.shareParts(baseCharacter);
            // cast and receive shadows
            cloneCharacter.enableShadows(true);
            cloneCharacter.setWeapon(0);
            cloneCharacter.setSkin(i);
            cloneCharacter.root.position.x = that.posX;
            cloneCharacter.root.position.z = that.posZ;
            cloneCharacter.root.rotation.y = Math.PI;
            that.scene.add(cloneCharacter.root);
            that.mainCharacter = cloneCharacter.root;
            k++;
          }
        }

        var gyro = new Gyroscope();
        gyro.add(that.camera);
        gyro.add(that.light, that.light.target);
        that.characters[Math.floor(nSkins / 2)].root.add(gyro);
      };

      baseCharacter.loadParts(configOgro);
    },
    // EVENT HANDLERS
    onWindowResize() {
      this.SCREEN_WIDTH = window.innerWidth;
      this.SCREEN_HEIGHT = window.innerHeight;
      this.renderer.setSize(this.SCREEN_WIDTH, this.SCREEN_HEIGHT);
      this.camera.aspect = this.SCREEN_WIDTH / this.SCREEN_HEIGHT;
      this.camera.updateProjectionMatrix();
    },
    handleTouchStart() {
      this.touchStatus = true;
    },
    handleTouchMove(e) {
      if (this.touchStatus) {
        let touches = e.changedTouches || e.targetTouches;
        if (touches && touches.length) {
          let pointBottom = document.body.clientHeight - touches[0].pageY;
          this.pointBottom = pointBottom > 120 ? 120 : pointBottom < 60 ? 60 : pointBottom;
          let pointLeft = touches[0].pageX;
          this.pointLeft = pointLeft > 120 ? 120 : pointLeft < 60 ? 60 : pointLeft;
        }
      }
    },
    handleTouchEnd() {
      this.touchStatus = false;
      this.pointBottom = 90;
      this.pointLeft = 90;
      this.stop();
    },
    attack() {
      if (this.controls.attack) return;
      this.stop();
      this.controls.attack = true;
      setTimeout(this.stopAttack, 1000);
    },
    stop() {
      this.controls.moveForward = false;
      this.controls.moveBackward = false;
      this.controls.moveLeft = false;
      this.controls.moveRight = false;
    },
    stopAttack() {
      this.controls.attack = false;
    },
    onKeyDown(event) {
      event.stopPropagation();
      switch (event.keyCode) {
        case 38: /*up*/
        case 87: /*W*/ 	this.controls.moveForward = true; break;
        case 40: /*down*/
        case 83: /*S*/ 	 this.controls.moveBackward = true; break;
        case 37: /*left*/
        case 65: /*A*/ this.controls.moveLeft = true; break;
        case 39: /*right*/
        case 68: /*D*/ this.controls.moveRight = true; break;
        case 67: /*C*/     this.controls.crouch = true; break;
        case 32: /*space*/ this.controls.jump = true; break;
        case 74: /*J*/  this.controls.attack = true; break;
        case 75: /*K*/ this.controls.salute = true; break;
      }
    },
    onKeyUp(event) {
      event.stopPropagation();
      switch (event.keyCode) {
        case 38: /*up*/
        case 87: /*W*/ this.controls.moveForward = false; break;
        case 40: /*down*/
        case 83: /*S*/ 	 this.controls.moveBackward = false; break;
        case 37: /*left*/
        case 65: /*A*/ 	 this.controls.moveLeft = false; break;
        case 39: /*right*/
        case 68: /*D*/ this.controls.moveRight = false; break;
        case 67: /*C*/     this.controls.crouch = false; break;
        case 32: /*space*/ this.controls.jump = false; break;
        case 74: /*J*/  this.controls.attack = false; break;
        case 75: /*K*/ this.controls.salute = false; break;
      }
    },
    animate() {
      requestAnimationFrame(this.animate);
      this.render();
    },
    render() {
      var delta = this.clock.getDelta();
      for (var i = 0; i < this.nCharacters; i++) {
        this.characters[i].update(delta);
      }
      this.renderer.render(this.scene, this.camera);
    }
  }
};
</script>
<style scoped>
.mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  color: var(--main-color);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
}

.controls,
.attack {
  width: 80px;
  height: 80px;
  border: 10px solid;
  border-radius: 100%;
  position: fixed;
  bottom: 50px;
  left: 50px;
  color: var(--main-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.attack {
  left: auto;
  right: 50px;
  border: none;
  background: url(../../static/resource/images/attack.svg) no-repeat center;
  background-size: contain;
}

.controls .point {
  width: 20px;
  height: 20px;
  background: var(--main-color);
  border-radius: 100%;
  position: fixed;
}
</style>


