import TWEEN from '@tweenjs/tween.js'

export default {
    install(Vue) {
        Vue.prototype.$tween = this;
        Vue.prototype.$tweener = TWEEN;
    },
    pause(tw) {
        tw.pause();
    },
    fade(target, to, during, delay, easing, onUpdate, onComplete) {
        if (!target || !to) {
            return;
        }
        if (during == null) {
            during = 260;
        }
        let tw = new TWEEN.Tween(target).to(to, during);
        if (delay) tw.delay(delay);
        tw.easing(easing || TWEEN.Easing.Linear.None)
        if (onUpdate) tw.onUpdate(onUpdate);
        if (onComplete) tw.onComplete(onComplete);
        tw.start();
        return tw;
    },
    animate(camera, controls, current, target, during, delay, onComplete) {
        let positionVar = {
            x1: camera.position.x,
            y1: camera.position.y,
            z1: camera.position.z,
            x2: controls.target.x,
            y2: controls.target.y,
            z2: controls.target.z
        };
        //关闭控制器
        controls.enabled = false;
        let tween = new TWEEN.Tween(positionVar).to({
            x1: current.x,
            y1: current.y,
            z1: current.z,
            x2: target.x,
            y2: target.y,
            z2: target.z
        }, during || 1000);

        tween.onUpdate(function () {
            camera.position.x = positionVar.x1;
            camera.position.y = positionVar.y1;
            camera.position.z = positionVar.z1;
            controls.target.x = positionVar.x2;
            controls.target.y = positionVar.y2;
            controls.target.z = positionVar.z2;
            controls.update();
        })

        tween.onComplete(function () {
            ///开启控制器
            controls.enabled = true;
            onComplete && onComplete();
        });

        tween.easing(TWEEN.Easing.Cubic.InOut);
        if (delay) tween.delay(delay);
        tween.start();
    }
}


